import React from 'react';
import './GuiaBoasPraticas.css'; 

const dicas = [
  {
    titulo: ' Nomeie variveis de forma clara',
    exemploRuim: `let x = 10;
let fn = () => {};`,
    exemploBom: `let quantidadeDeAlunos = 10;
let calcularImposto = () => {};`,
    explicacao: 'Use nomes descritivos e completos que indiquem o propsito da varivel ou funo. Evite abreviaes desnecessrias.',
  },
  {
    titulo: ' Quebre o cdigo em funes menores (SRP)',
    exemploRuim: `// Tudo misturado na mesma funo
function processarPedido(pedido) {
  // 1. Validao do pedido
  if (!pedido.id || pedido.valor <= 0) {
    throw new Error('Pedido invlido.');
  }
  // 2. Clculo do total
  let total = pedido.itens.reduce((acc, item) => acc + item.preco * item.quantidade, 0);
  // 3. Aplicao de desconto
  if (pedido.clienteFidelidade) {
    total *= 0.9; // 10% de desconto
  }
  // 4. Envio do pedido
  console.log('Enviando pedido...', total);
  // 5. Atualizao de estoque
  // ...
}`,
    exemploBom: `// Funes com responsabilidades nicas
function validarPedido(pedido) {
  if (!pedido.id || pedido.valor <= 0) {
    throw new Error('Pedido invlido.');
  }
  return true;
}

function calcularTotal(pedido) {
  let total = pedido.itens.reduce((acc, item) => acc + item.preco * item.quantidade, 0);
  if (pedido.clienteFidelidade) {
    total *= 0.9;
  }
  return total;
}

function enviarPedido(pedido, total) {
  console.log('Enviando pedido com total:', total);
  // Lgica de envio...
}

function processarPedidoRefatorado(pedido) {
  validarPedido(pedido);
  const totalCalculado = calcularTotal(pedido);
  enviarPedido(pedido, totalCalculado);
  // atualizarEstoque(pedido);
}`,
    explicacao: 'Siga o Princpio da Responsabilidade nica (SRP): cada funo deve ter apenas um motivo para mudar. Isso torna o cdigo mais fcil de entender, testar e manter.',
  },
  {
    titulo: ' Comente o "porqu", no o "o qu"',
    exemploRuim: `// Inicializa a varivel 'i' com 0
for (let i = 0; i < 10; i++) {
  // Imprime o valor de 'i'
  console.log(i);
}`,
    exemploBom: `// Correo de bug: Certificar-se de que o loop comea em 0 para incluir o primeiro elemento do array.
for (let i = 0; i < array.length; i++) {
  console.log(array[i]);
}`,
    explicacao: 'O bom cdigo se explica. Comentrios so para explicar decises complexas, otimizaes, ou o "porqu" de uma escolha que no  imediatamente bvia, e no para reescrever o que o cdigo j mostra.',
  },
  {
    titulo: ' Evite repetio (Princpio DRY)',
    exemploRuim: `function exibirErroConexao() {
  alert("Erro de conexo!");
  console.error("Conexo falhou.");
}

function exibirErroServidor() {
  alert("Erro no servidor!");
  console.error("Servidor inacessvel.");
}`,
    exemploBom: `function exibirMensagemErro(tipoErro, detalhes) {
  alert(\`Erro de \${tipoErro}!\`);
  console.error(\`\${tipoErro} falhou: \${detalhes}\`);
}

// Uso:
exibirMensagemErro("conexo", "Conexo de rede perdida.");
exibirMensagemErro("servidor", "API indisponvel.");`,
    explicacao: 'DRY (Don\'t Repeat Yourself - No se Repita): Se voc est escrevendo o mesmo cdigo vrias vezes, transforme-o em uma funo ou um mdulo reutilizvel. Isso reduz bugs e facilita a manuteno.',
  },
  {
    titulo: ' Mantenha o cdigo formatado (indentao)',
    exemploRuim: `function minhaFuncao() {
if (condicao) {
console.log("Ol");
}
else {
console.log("Adeus");
}
}`,
    exemploBom: `function minhaFuncao() {
  if (condicao) {
    console.log("Ol");
  } else {
    console.log("Adeus");
  }
}`,
    explicacao: 'Use indentao consistente para mostrar a estrutura do seu cdigo. Ferramentas como Prettier ou ESLint podem formatar automaticamente.',
  },
  {
    titulo: ' Evite nmeros "mgicos"',
    exemploRuim: `// Clculo do imposto
let imposto = salario * 0.15;`,
    exemploBom: `const TAXA_IMPOSTO = 0.15; // Define a taxa de imposto como uma constante
let imposto = salario * TAXA_IMPOSTO;`,
    explicacao: 'Nmeros "mgicos" so valores literais cujo significado no  imediatamente bvio. Use constantes bem nomeadas para dar clareza e facilitar futuras alteraes.',
  },
  {
    titulo: ' Valide entradas de funes',
    exemploRuim: `function dividir(a, b) {
  return a / b; // E se b for 0?
}`,
    exemploBom: `function dividir(a, b) {
  if (b === 0) {
    console.error("Erro: Diviso por zero!");
    return null; // Ou jogue um erro, dependendo do contexto
  }
  if (typeof a !== 'number' || typeof b !== 'number') {
    console.error("Erro: Ambos os argumentos devem ser nmeros.");
    return null;
  }
  return a / b;
}`,
    explicacao: 'Sempre que possvel, valide os parmetros que suas funes recebem para evitar comportamentos inesperados e erros em tempo de execuo.',
  },
  {
    titulo: ' Evite efeitos colaterais inesperados',
    exemploRuim: `let totalGlobal = 0;
function adicionar(valor) {
  totalGlobal += valor; // Modifica uma varivel global
}`,
    exemploBom: `function adicionar(valor1, valor2) {
  return valor1 + valor2; // Retorna um novo valor, sem alterar estado externo
}

// Uso:
let novoTotal = adicionar(totalAtual, 5);`,
    explicacao: 'Funes "puras" (que no modificam variveis externas e sempre retornam o mesmo resultado para os mesmos inputs) so mais fceis de testar e entender. Minimize efeitos colaterais.',
  },
  {
    titulo: ' Otimize para legibilidade, no para velocidade prematura',
    exemploRuim: `for (let i=0;i<array.length;i++){doSomething(array[i])}`,
    exemploBom: `for (let i = 0; i < array.length; i++) {
  const item = array[i];
  doSomething(item);
}`,
    explicacao: 'No se preocupe em otimizar o cdigo para ser ultra-rpido antes que voc saiba que ele  um gargalo de desempenho. Priorize a clareza e a legibilidade. Compiladores e interpretadores modernos so muito bons em otimizaes.',
  },
  {
    titulo: ' Use `const` e `let`, evite `var`',
    exemploRuim: `var nome = "Alice";
var idade = 30; // 'var' tem escopo de funo, no de bloco
if (true) {
  var nome = "Bob"; // Redeclara 'nome' globalmente
}`,
    exemploBom: `const NOME_APLICATIVO = "MeuApp"; // Valor no muda
let contador = 0; // Valor pode ser reatribudo
if (true) {
  let tempIdade = 25; // 'let' tem escopo de bloco, mais seguro
}`,
    explicacao: '`const` para variveis que no sero reatribudas (imutveis). `let` para variveis que sero reatribudas (mutveis). `var` tem problemas de "hoisting" e escopo que podem levar a bugs.',
  },
];

const GuiaBoasPraticas = () => {
  return (
    <div className="guia-container"> {/* MODIFICADO: Chamando a classe do CSS externo */}
      <h1 className="guia-titulo"> Guia de Boas Prticas em Cdigo</h1> {/* MODIFICADO: Chamando a classe do CSS externo */}
      <p className="intro-text"> {/* MODIFICADO: Chamando a classe do CSS externo */}
        Aprenda a escrever cdigo mais limpo, legvel e eficiente com exemplos simples e dicas visuais. Ideal para quem est comeando!
      </p>
      {dicas.map((dica, index) => (
        <div key={index} className="dica-card"> {/* MODIFICADO: Chamando a classe do CSS externo */}
          <h2 className="dica-titulo">{dica.titulo}</h2> {/* MODIFICADO: Chamando a classe do CSS externo */}
          <p className="explicacao-text">{dica.explicacao}</p> {/* MODIFICADO: Chamando a classe do CSS externo */}
          <div className="code-examples-grid"> {/* MODIFICADO: Chamando a classe do CSS externo */}
            <div>
              <p className="status-bad">Ruim:</p> {/* MODIFICADO: Chamando a classe do CSS externo */}
              <pre className="code-block bad-example">{dica.exemploRuim}</pre> {/* MODIFICADO: Chamando a classe do CSS externo */}
            </div>
            <div>
              <p className="status-good">Melhor:</p> {/* MODIFICADO: Chamando a classe do CSS externo */}
              <pre className="code-block good-example">{dica.exemploBom}</pre> {/* MODIFICADO: Chamando a classe do CSS externo */}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default GuiaBoasPraticas;